import React from 'react';
import './translations';
import './questions';
import './App.css';
import { Box, Button, Checkbox, FormControlLabel, FormGroup, FormLabel } from "@mui/material";
import QuizIcon from '@mui/icons-material/Quiz';
import { Image } from "@mui/icons-material";

const translations = window.translations;
const questions = window.questions.map((item, index) => ({ ...item, id: index }));
const letters = ['a', 'b', 'c', 'd', 'e', 'f'];

const VIEWS = {
    MAIN: 'MAIN',
    YACHT_TESTS_MAIN: 'YACHT_TESTS_MAIN',
    YACHT_TESTS: 'YACHT_TESTS',
};

function shuffle(array) {
    let currentIndex = array.length;

    // While there remain elements to shuffle...
    while (currentIndex != 0) {

        // Pick a remaining element...
        let randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
            array[randomIndex], array[currentIndex]];
    }
};

function App() {
    const [view, setView] = React.useState(VIEWS.YACHT_TESTS_MAIN);
    const [randomQuestions, setRandomQuestions] = React.useState(false);

    const [questionsCount, setQuestionsCount] = React.useState(questions.length);
    const [questionsOrder, setQuestionsOrder] = React.useState(null);
    const [currentPosition, setCurrentPosition] = React.useState(null);
    const [answers, setAnswers] = React.useState({});

    const goTo = (viewToSet) => {
        setView(viewToSet);

        if (viewToSet === VIEWS.YACHT_TESTS) {
            let order = questions.map((item, index) => index);
            if (randomQuestions) {
                shuffle(order);
                order = order.splice(0, 75);
            }

            setAnswers({});
            setQuestionsOrder(order);
            setCurrentPosition(0);
            setQuestionsCount(order.length);
        }
    };

  const renderMain = () => {
      if (view !== VIEWS.MAIN) {
          return;
      }

      return (
          <Box id="main">
              <Button onClick={() => goTo(VIEWS.YACHT_TESTS_MAIN)}>{translations.startTest}</Button>
          </Box>
      );
  };

  const renderTestsMain = () => {
      if (view !== VIEWS.YACHT_TESTS_MAIN) {
          return;
      }

      return (
          <Box id="yach-tests-main"
              sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  width: '100%',
                  height: '100%',
                  alignItems: 'center',
                  gap: '16px',
                  padding: '16px',
              }}>
              <Box  >
                  <FormGroup>
                      <FormControlLabel control={<Checkbox
                          checked={randomQuestions}
                          onClick={() => setRandomQuestions(!randomQuestions)}
                      />} label={translations.random} />
                  </FormGroup>
              </Box>
              <Box sx={{  }}>
                  <Button
                      variant="contained"
                      endIcon={<QuizIcon />}
                      onClick={() => goTo(VIEWS.YACHT_TESTS)}
                  >
                      {translations.startTest}
                  </Button>

                  {/*<Button onClick={() => goTo(VIEWS.MAIN)}>{translations.goBack}</Button>*/}
              </Box>
          </Box>
      );
  };

  const renderQuestion = () => {
      const question = questions[questionsOrder[currentPosition]];
      if (!question) {
          return;
      }

      const onAnswer = (optionIndex, isRight) => {
          if (answers[question.id] !== undefined) {
              delete answers[question.id];
          } else {
              answers[question.id] = optionIndex;
          }

          setAnswers({
              ...answers,
          });
      };

      const renderPart = (localizedQuestion) => {
          return (
              <Box>
                  <Box sx={{
                      fontWeight: 'bold',
                      fontSize: '20px',
                  }}
                  >{currentPosition + 1}) {localizedQuestion.title}</Box>
                  {localizedQuestion.options.map((option, optionIndex) =>
                      <Box sx={{
                          margin: '4px 16px',
                          color: answers[question.id] === optionIndex
                              ? (option.isRight ? 'green' : 'red')
                              : 'black'
                      }}>
                          <FormGroup>
                              <FormControlLabel control={<Checkbox
                                  disabled={answers[question.id] !== undefined && answers[question.id] !== optionIndex}
                                  checked={answers[question.id] === optionIndex}
                                  onClick={() => onAnswer(optionIndex, option.isRight)}
                              />} label={`${letters[optionIndex]}) ${option.text}`} />
                          </FormGroup>
                      </Box>
                  )}
              </Box>
          );
      }

      return (
          <Box sx={{
              display: 'flex',
              flexDirection: 'column',
              margin: '20px',
          }}>
            <Box sx={{
                display: 'flex',
                flexDirection: 'column',
            }}>
                {renderPart(question.pol)}
                {renderPart(question.rus)}
            </Box>
            <Box sx={{
                margin: '10px 40px',
            }}>
                {question.images?.map((image) =>
                    <img height={'auto'} width={'auto'} src={`http://localhost:3006/images/${image}`} />
                )}
            </Box>

            <Box sx={{
                display: 'flex',
                margin: '10px 20px',
            }}>
                {currentPosition > 0 && <Button
                    sx={{ marginRight: '10px' }}
                      size="large"
                      onClick={() => {
                          const newPosition = currentPosition - 1;

                          setCurrentPosition(newPosition);
                      }}>{translations.previous}</Button>
                }
                  <Button
                      variant="contained"
                      size="large"
                      onClick={() => {
                          const newPosition = currentPosition + 1;

                          setCurrentPosition(newPosition);
                      }}>{translations.next}</Button>
            </Box>
          </Box>
      );
  };

  const renderTests = () => {
      if (view !== VIEWS.YACHT_TESTS) {
          return;
      }

      const renderQuestionOrResult = () => {
          if (currentPosition >= questionsCount) {
              let score = 0;

              Object.keys(answers).forEach((questionId) => {
                  const question = questions.find(item => item.id.toString() === questionId.toString());

                  if (question?.rus?.options[answers[questionId]]?.isRight) {
                      score++;
                  }
              });

              return (<Box sx={{}}>
                  <Box sx={{
                      textAlign: 'center',
                      fontSize: '30px',
                      margin: '20px',
                  }}>{translations.results}</Box>
                  <Box sx={{
                      textAlign: 'center',
                      fontSize: '60px',
                      margin: '30px',
                      color: score === questionsCount ? 'green' : 'black',
                  }}>{score}/{questionsCount}</Box>
              </Box>);
          }

          return renderQuestion();
      }

      return (
          <Box id="yach-tests" sx={{
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'space-between',
          }}>
              {renderQuestionOrResult()}
              <Button
                  onClick={() => goTo(VIEWS.YACHT_TESTS_MAIN)}>{translations.goToMain}</Button>
          </Box>
      );
  };

  return (
    <Box sx={{
        height: '100%',
        width: '100%',
        display: 'flex',
    }}>
        <Box sx={{
            width: '80%',
            padding: '20px',
            border: '1px solid darkgrey',
            margin: 'auto',
            boxShadow: '1px 1px 5px 2px lightgrey',
            borderRadius: '4px',
        }}>
            {renderMain()}
            {renderTestsMain()}
            {renderTests()}
        </Box>
    </Box>
  );
}

export default App;
